* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  background-image: url("./images/background.png");
  color: white;
  font-family: Epilogue, sans-serif;
}

header {
  font-size: 2.5rem;
  margin: 50px 0 20px;
  text-align: center;
}

h2 {
  border: 1px solid white;
  border-radius: 20px;
  font-size: 2.5rem;
  margin: 50px;
  padding: 25px;
  text-align: center;
}

.planets {
  display: flex;
  justify-content: space-around;
  margin: 0 20px 100px;
}

.planet-card {
  align-items: center;
  display: flex;
  flex-direction: column;
}

.planet-card > div {
  display: flex;
  flex: 1;
  flex-direction: column;
  justify-content: center;
}

.planet-card img {
  width: 100%;
}

.planet-card h3 {
  font-weight: 300;
}

.mercurio {
  width: 6.2%;
}

.venus {
  width: 8%;
}

.terra {
  width: 8.2%;
}

.marte {
  width: 6.7%;
}

.jupiter {
  width: 25.7%;
}

.saturno {
  width: 20.1%;
}

.urano {
  width: 12.8%;
}

.netuno {
  width: 12.4%;
}

.missions {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
}

.mission-card {
  border: 1px solid white;
  border-radius: 20px;
  margin: 20px 30px;
  padding: 20px;
  width: 25%;
}

.mission-card h3 {
  font-size: 2rem;
}

hr {
  margin: 20px 0 30px;
}

.mission-card h4 {
  font-size: 1.5rem;
  font-weight: 300;
}

footer {
  margin: 30px;
  text-align: center;
}

.footer img {
  height: 100px;
}
